let userDisplayInfo = {};

export const createGoogleAuthInfoObject = (data) => {
  userDisplayInfo = {
    id: data.user.uid,
    email: data.user.email,
    name: data.user.displayName,
    image: data.user.photoURL,
  };
  console.log(userDisplayInfo);
};

