import { provider, signInWithPopup, auth } from "./init";
import { createGoogleAuthInfoObject } from "../model";

export let googleLogin = () => {
  signInWithPopup(auth, provider)
    .then((res) => {
      console.log(res);
      createGoogleAuthInfoObject(res);
    })
    .catch((err) => {
      console.log(err);
    });
};
